.card-order {
  &__items {
    padding: 10px 24px;
  }

  &__flex {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
}
.order-create {
  &__title {
    margin-bottom: 30px;
    font-size: 30px;
    text-align: center;
  }

  &__img {
    display: flex;
    justify-content: center;
  }

  &__photo {
    padding: 50px;
  }

  &__badge {
    background-color: #81C784;
  }

  &__control {
    max-width: 80px;
  }

  &__card-title {
    font-size: 20px;
  }

  &__card-position {
    display: flex;
    justify-content: space-between;
  }

  &__price {
    align-self: center;
    font-size: 20px;
  }

  &__subcard {
    display: flex;
    justify-content: space-between;
    padding: 10px 24px;
    font-size: 25px;
  }

  &__btn {
    display: flex;
    justify-content: flex-end;
  }

  &__link {
    padding: 0 12px;
  }

}