.input-field label {
  font-size: 20px;
}

.card .card-title {
  font-size: 40px;
}

.center {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.login {
  margin: 20px auto;
  padding: 20px;
  width: 50%;
  border: 2px solid gray;
  border-radius: 5px;

  &__title {
    margin: 20px 0 50px;
    text-align: center;
  }

  &__label {
    font-size: 30px;
  }

  &__link {
    &-center {
      margin: 0 auto;
    }
  }

  &__link:hover {
    font-size: 17px;

    transition: font-size .2s;
  }

  &__btns {
    display: flex;
    justify-content: space-between;
  }

  &__btn {
    &-autorization {
      padding: 0 20px;
    }

    
  }
}