.file-field .btn {
  float: right;
}

.file-field .file-path-wrapper {
  padding-left: 0;
  padding-right: 10px;
}

.card .card-content p {
  margin-top: 10px;
}

.card .card-action {
  padding: 0 24px;
}

.card-action {
  display: flex;
  justify-content: space-between;
}

span.badge {
  padding: 0 6px;
  min-width: 0;

  font-size: 17px;
  border-radius: 50%;
}

.profile {
  &__title {
    font-size: 30px;

    &-flex {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  &__img {
    display: flex;
    justify-content: center;
  }

  &__photo {
    padding: 50px;
  }

  &__badge {
    background-color: #81C784;
  }

  &__control {
    max-width: 80px;
  }

  &__card-title {
    font-size: 20px;
  }

  &__card-position {
    display: flex;
    justify-content: space-between;
  }

  &__price {
    align-self: center;
    font-size: 20px;
  }

  &__subcard {
    display: flex;
    justify-content: space-between;
    padding: 10px 24px;
    font-size: 25px;
  }

  &__btn {
    display: flex;
    justify-content: flex-end;
  }

  &__link {
    padding: 0 12px;
  }

}